$myBorder: 1px solid;
$myTextDeco: none;

.navbar {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 70px;

    a {
      text-decoration: $myTextDeco;
      font-size: 20;
      border: $myBorder;
      border-radius: 4px;
      padding: 15px 20px;
      width: 65px;
      height: 20px;
      margin: 0px 15px;
    }
}